.footer {
    width: 100%;
    height: 300px;
    padding: 50px;
    box-sizing: border-box;
    background-color: gainsboro;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    color: black;
} 
.footer-title {
    font-weight: bold;
}
.footerBox1 {
    width: calc(100%*2/5);
}
.footerBox2 {
    width: calc(100%*1/5);
}
.footerBox1 p {
    margin-bottom: 15px;
}
.footerBox2 nav ul {
    list-style-type: none;
    padding: 0;
}
.footerBox2 nav ul li {
    margin-top: 15px;
    padding: 0;
}