.home {
    width: 100%;
    height: 100vh;
    /* padding: 80px 0; */
    display: flex;
}
.home > * {
    width: calc(100%/2);
}
.content {
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}
.content >*{
    margin: 20px 0;
}
.home-img {
    background-image: url(../../assets/homePage.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}