.work {
    width: 100%;
    height: 200vh;
}
.work-section {
    width: 100%;
    height: 100vh;
    display: flex;
    padding: 20px;
}
.work-section >*{
    width: calc(100%/2);
    height: 100%;
    /* padding: 80px; */
    box-sizing: border-box;
}
button {
    padding: 15px 20px;
    border-radius: 10px;
    background-color: white;
    color: black;
    border: 1px solid black;
    font-size: 18px;
}
.fab {
    font-size: 20px;
}
.download-img, .connect-img {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}
.download-img {
    background-image: url(../../assets/download.jpg);
}
.connect-img {
    background-image: url(../../assets/connectBanking.jpg);
}