#about {
    width: 100%;
    height: 100vh;
    /* padding: 80px 0; */
}
.about-header {
    margin: 0 auto;
    text-align: center;
    padding: 50px 300px;
}
.about-header p {
    margin: 20px 0;
}
.about-list ul {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    list-style-type: none;
    text-align: center;
}
.about-list ul >*{
    /* width: calc(100%/3); */
    width: 300px;
    height: 500px;
    margin: 0 15px;
}
.info-img, .data-img, .card-img {
    height: 300px;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.info-img {
    background-image: url(../../assets/info.jpg);
}
.data-img {
    background-image: url(../../assets/data.jpg);
}
.card-img {
    background-image: url(../../assets/card.jpg);
}