.header {
    display: flex;
    justify-content: space-between;
    height: 90px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 20px;
    /* padding: 0 15px; */
    /* position: fixed; */
}
img {
    width: 90px;
    height: 90px;
    line-height: 90px;
}
.active-btn {
    padding: 15px 20px;
    border-radius: 10px;
    background-color: black;
    color: white;
    border: 1px solid black;
    font-size: 20px;
}
.header ul {
    display: flex;
    align-items: center;
    line-height: 80px;
    text-decoration: none;
}
a {
    text-decoration: none;
    color: black;
}
.header ul li {
    color: black;
    margin-right: 10px;
    list-style-type: none;
    text-decoration: none;
}
